@use "../../scss/breakPoints/breakPoints.scss" as *;
@use "../../scss/colors/colors.scss" as *;

.wrapper {
   padding: 0 16px;
   position: relative;
   margin-bottom: 100px;

   @include breakPointsMax(medium) {
      margin-bottom: 60px;
   }

   .products-display__title {
      margin: 16px 0;
      text-align: center;
      @include breakPointsMax(small) {
         font-size: 14px;
      }
   }
   .left-right-arrows {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      font-size: 32px;
      cursor: pointer;
      &.right-icon {
         right: 16px;
      }
   }
   width: 100vw;
   .products-display {
      overflow: scroll;
      scrollbar-width: none;

      .products-display__cards {
         display: grid;
         justify-content: center;
         grid-template-columns: repeat(6, 260px);
         grid-auto-rows: 400px;
         gap: 16px;

         @include breakPointsMax(xxl) {
            justify-content: start;
         }
         @include breakPointsMax(medium) {
            grid-template-columns: repeat(6, 200px);
            grid-auto-rows: 380px;
            gap: 8px;
         }
         @include breakPointsMax(small) {
            grid-template-columns: repeat(6, 135px);
            grid-auto-rows: 280px;
         }

         .products-display__card {
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            border: 1px solid $secondary_color;
            border-radius: 8px;
            cursor: pointer;
            padding: 16px;

            @include breakPointsMax(small) {
               padding: 8px;
            }

            &:hover {
               border-color: $primary_color;
            }

            .card__image {
               height: 70%;
               width: 100%;
               background: grey;
               border-radius: 8px;
               overflow: hidden;

               @include breakPointsMax(small) {
                  height: 55%;
               }

               img {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
               }
            }
            .card__text {
               p {
                  margin: 4px 0;
               }
               .brand {
                  font-weight: bold;
               }
               .product-name {
                  color: $mediumGrey;
                  width: 200px;
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;

                  @include breakPointsMax(medium) {
                     width: 160px;
                  }
                  @include breakPointsMax(small) {
                     width: 100px;
                     font-size: 14px;
                  }
               }
               .price {
                  @include breakPointsMax(small) {
                     font-size: 12px;
                  }
                  span {
                     &:nth-of-type(1) {
                        margin-right: 8px;
                        @include breakPointsMax(small) {
                           margin-right: 4px;
                        }
                     }
                     &:nth-of-type(2) {
                        margin-left: 8px;
                        font-size: 14px;
                        @include breakPointsMax(small) {
                           margin-left: 4px;
                        }
                     }
                  }
               }
               .off {
                  color: rgb(17, 192, 17);
               }
               .old-price {
                  color: $mediumGrey;
                  text-decoration: line-through;
               }
               .rating {
                  background: $secondary_color;
                  height: 22px;
                  width: 44px;
                  color: #fff;
                  display: flex;
                  align-items: center;
                  justify-content: space-evenly;
                  font-size: 12px;
                  border-radius: 22px;

                  @include breakPointsMax(small) {
                     height: 18px;
                     width: 36px;
                     font-size: 10px;
                  }

                  p:nth-of-type(2) {
                     height: 100%;
                     display: flex;
                     align-items: center;
                  }
               }
            }
         }
      }
   }
   .products-display__btn {
      display: flex;
      justify-content: center;
      margin: 32px 0;

      button {
         height: 32px;
         width: 96px;
         background: transparent;
         outline: none;
         border: 2px solid $secondary_color;
         color: $secondary_color;
         text-transform: uppercase;
         cursor: pointer;
         font-weight: bold;
         border-radius: 8px;

         &:hover {
            background-color: $secondary_color;
            color: #fff;
         }
      }
   }
}
